@font-face {
  font-family: Foo;
  src: url("../font/foo.otf") format("opentype");
}

@font-face {
  font-family: Duality;
  src: url("../font/DUALITY_.ttf") format("truetype");
}

body{
  margin: 0;
  padding: 0;
  font-family: Duality, Lucida Sans Unicode;
  background-color: #00091c;
  color: #fff;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
  flex-grow: 1;
  font-family: Duality,Poppins, Lucida Sans Unicode;
  font-size:20px;
  overflow: hidden;
}

.home-card {
  margin: 0;
  padding: 0;
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 20px;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.referral-link-box {
  width: 100%;
}

.referral-link-box input{
  color:#fff;
  border:solid 1px #fff;
  border-radius: 4px;
  text-shadow: rgb(43, 0, 43) 0 0 5px, rgb(204, 0, 204) 0 0 20px, rgb(255, 0, 255) 0 0 10px;
}

.display-option-placeholder{
  margin: 0 20px 0 0;
}

.display-option{
  float: right;
}

.display-option-textfield fieldset{
  color:#fff;
  border:0;
  text-shadow: rgb(5, 43, 0) 0 0 5px, rgb(0, 204, 40) 0 0 20px, rgb(0, 255, 76) 0 0 10px;
}

.display-option-textfield .select-container{
  color:#fff;
  text-shadow: rgb(5, 43, 0) 0 0 5px, rgb(0, 204, 40) 0 0 20px, rgb(0, 255, 76) 0 0 10px;
}

.global-stats{
  padding-top: 30px;
  font-size:20px;
}

.large-text {
  font-size:28px;
}

.medium-text{
  font-size:23px;
}

.glowing-pink{
  font-family: Foo, Duality, sans-serif;
  text-shadow: rgb(43, 0, 43) 0 0 5px, rgb(204, 0, 204) 0 0 20px, rgb(255, 0, 255) 0 0 10px;
}

.glowing-blue{
