.image-hero-small {
  width:120px;
  min-height:120px;
  margin-top:50px;
  text-align: center;
}

.image-skill-small {
  width:30px;
  height:30px;
  border-radius: 5px;
}

.center {
  text-align: center;
}

.hero-plate {
  text-align: center;
  flex-grow: 1;
  font-family: Poppins, sans-serif;
  background-color: #00091c;
  color: #fff;
  display: flex;
  flex-direction: column;
  padding:0;
  border-radius: 10px;
  overflow: hidden;
  margin:0;
}

.hero-placeholder {
  font-family: Foo,Poppins, sans-serif;
  padding:0;
  position: relative;
  margin: 25px;
  width:250px;
}

.hero-attack {
  position: absolute;
  top: -20px;
  left: 0;
  font-size:26px;
  font-weight: bold;
  border-radius: 10px;
  min-width:50px;
  padding:3px;
  background-color:#c40000;
}

.hero-role {
  position: absolute;
  top: 185px;
  left: -20px;
  font-size:12px;
  font-weight: bold;
  border-radius: 10px;
  min-width:50px;
  padding:0;
}

.hero-defence {
  position: absolute;
  top: -20px;
  right: 0;
  font-size:26px;
  font-weight: bold;
  border-radius: 10px;
  min-width:50px;
  padding:3px;
  background-color:#01bc9a;
  z-index: 3000;
}

.hero-rarity {
  position: absolute;
  top: 190px;
  right: -10px;
  font-size:16px;
  font-weight: bold;
  border-radius: 5px;
  min-width:50px;
  padding:3px;
  background-color:#000;
  z-index: 3000;
  border: 1px solid #a39fa3;
}


.hero-health {
  position: absolute;
  bottom: -20px;
  left: 0;
  font-size:26px;
  font-weight: bold;
  border-radius: 10px;
  min-width:50px;
  padding:3px;
  background-color:rgb(104, 0, 0);
}

.hero-healing {
  position: absolute;
  bottom: -20px;
  right: 0;
  font-size:26px;
  font-weight: bold;
  border-radius: 10px;
  min-width:50px;
  padding:3px;
  background-color:rgb(19, 130, 5);
}

.hero-skill {
  box-shadow:none;
  text-shadow:none;
  font-size:11px;
  color:#fff;
  width:100%;
  min-height:100px;
  border: 1px solid #a39fa3;
  border-bottom: 0;
  margin:10px 0 0;
}

.hero-skill-banner {
  font-family: Foo,Duality,Poppins, sans-serif;
  position: relative;
  font-size:11px;
  background-color:#000;
  border-radius: 10px;
  padding:5px;
  top:-10px;
  border: 1px solid #a39fa3;
}

.hero-name-banner {
  font-family: Foo,Duality,Poppins, sans-serif;
  font-size:22px;
  border-radius: 10px;
  padding:0;
  min-width:10px;
}

.hero-plate-count {
  font-family: Foo,Poppins, sans-serif;
  position: relative;
  bottom: 10px;
  right: 10px;
}

.image-stats-small {
  width:35px;
  height:35px;
}

.hero-stats-plate {
  padding:10px;
  margin-top:15px;
  margin-bottom:0;
}

.hero-stats{
  font-family: Foo,Poppins, sans-serif;
  width:50px;
  height:50px;
  position:relative;
}

.hero-stats .image-stats-small{
  position:absolute;
  top:0;
  left:0;
  z-index: 200;
}

.hero-stats-title{
  position:relative;
  font-size:22px;
  top:7px;
  left:3px;
  z-index: 300;
  text-shadow:none;
  font-weight: bold;
}

.hero-skill-item {
  display: flex;
  flex-direction:row;
  width:100%;
  text-align:left;
  margin-bottom:5px;
}

.hero-skill-item .icon{
  margin-left:10px;
}

.hero-skill-item .description{
  font-family: Duality,Poppins, sans-serif;
  margin-left:20px;
}

.hero-skill-item .title{
  font-family: Foo,Poppins, sans-serif;
  margin-left:0;
}

