.intro-card {
  width: 100%;
  margin:0 50px 50px;
}

.shine {
  border-radius: 10px;
  animation: glowAnimation 3s ease-in-out infinite;
}

.moveUpDown {
  animation: moveUpDownAnimation 5s linear infinite;
  position: relative;
  left: 0;
  bottom: 0;
}

.full-width{
  width:100%;
}

@keyframes glowAnimation{
  50%  { box-shadow: 0 0 200px #fff; }
}

@keyframes moveUpDownAnimation {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 50px;
  }
}

@keyframes flickerAnimation {
  0%   { opacity:1; }
  50%  { opacity:0.5; }
  100% { opacity:1; }
}

@-o-keyframes flickerAnimation{
  0%   { opacity:1; }
  50%  { opacity:0.5; }
  100% { opacity:1; }
}

@-moz-keyframes flickerAnimation{
  0%   { opacity:1; }
  50%  { opacity:0.5; }
  100% { opacity:1; }
}

@-webkit-keyframes flickerAnimation{
  0%   { opacity:1; }
  50%  { opacity:0.5; }
  100% { opacity:1; }
}

.animate-flicker {
  -webkit-animation: flickerAnimation 3s infinite;
  -moz-animation: flickerAnimation 3s infinite;
  -o-animation: flickerAnimation 3s infinite;
  animation: flickerAnimation 3s infinite;
}

